


































import Vue from "vue";
import { mapActions, mapMutations } from "vuex";
import { Login as LoginType } from "../service/interfaces";

export default Vue.extend({
  name: "Login",
  data() {
    return {
      model: <LoginType>{},
    };
  },
  methods: {
    ...mapActions(["login"]),
    ...mapMutations(["setToken"]),
    async submit() {
      try {
        await this.login(this.model);
        this.$router.push("/home").catch((err) => {
          console.error(err);
        });
      } catch (err) {
        console.error(err);
      }
    },
  },
});
